@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,700|Roboto:400,700&display=swap&subset=latin-ext);
*{margin:0;padding:0;box-sizing:border-box}html{font-family:"Roboto";color:#333}

.wrapper{width:95%;margin:auto}.wrapper__results-info{text-align:center;margin-top:35px;font-size:20px}

.input{border:2px solid #eee;box-shadow:2px 2px 5px rgba(30,88,255,0.3);background-color:white;font-family:"Open Sans";color:#333;margin:10px 10px;text-transform:lowercase;padding:3px 10px;font-weight:normal;letter-spacing:normal;flex-basis:40%;max-width:auto;line-height:1.5}@media (min-width: 640px){.input{font-size:18px}}

.button{border:2px solid #eee;box-shadow:2px 2px 5px rgba(30,88,255,0.3);background-color:white;font-family:"Open Sans";color:#333;margin:10px 10px;text-transform:uppercase;padding:3px 10px;font-weight:bold;letter-spacing:1.5px;flex-basis:100px;max-width:auto;line-height:normal;border-radius:5px;-webkit-transition:.3;transition:.3}.button:hover{background-color:#dedede;border:2px solid #dedede}@media (min-width: 640px){.button{font-size:18px}}

.form{display:flex;justify-content:center;align-content:center;background-color:#fff;margin:20px 0 40px}

.item{margin:10px 0px;box-shadow:2px 2px 5px 2px rgba(30,88,255,0.3);padding:10px 5px;width:300px;position:relative}.list__item{list-style:none;display:flex;flex-direction:column}.item__meal-name{text-align:center;font-size:20px;padding-bottom:20px}.item__details{display:flex;justify-content:space-around;align-items:center;flex-grow:1;min-height:200px;width:95%;margin:auto;margin:0 auto 20px}.item__photo-wrapper{display:flex;align-items:flex-start;justify-content:center}.item__photo{width:120px;height:120px;border-radius:50%;margin-right:10px}.item__ingridents{flex-grow:1}.item__ingrident{display:flex;justify-content:space-between;font-size:9px}.item__ingrident-name{text-align:left;padding-right:2px;min-width:60px}.item__measure{text-align:right;padding-left:2px}.item__buttons-wrapper{display:flex;margin-bottom:10px}.item__button{border:2px solid #eee;box-shadow:2px 2px 5px rgba(30,88,255,0.3);background-color:white;font-family:"Open Sans";color:#333;margin:10px 10px;text-transform:uppercase;padding:3px 10px;font-weight:bold;letter-spacing:normal;font-size:13px;flex-basis:none;max-width:120px;line-height:normal;display:block;text-align:center;margin:0 auto 0px;text-decoration:none}.item__button:hover{background-color:#dedede;border:2px solid #dedede}

.popup{position:fixed;display:flex;justify-content:center;width:100vw;min-height:100vh;max-height:100%;overflow-y:scroll;z-index:1;top:0;left:0;background-color:rgba(200,200,200,0.6);margin:0}.popup .list__item{background-color:#fff;flex-basis:90%;max-width:800px;margin:20px;height:100%}.popup .item__meal-name{font-size:28px;padding:10px;margin-bottom:10px}.popup .item__instruction-text{font-size:16px;margin-top:10px;padding:20px}.popup .item__details{justify-content:space-evenly;flex-grow:0;max-width:500px}.popup .item__ingridents{max-width:250px}.popup .item__ingrident{font-size:12px}.close-button{position:absolute;cursor:pointer;background-color:transparent;border:0;outline:none;top:5px;right:10px}.close-button__box{position:relative;display:inline-block;width:20px;height:20px}.close-button__inner{position:absolute;display:inline-block;width:100%;top:50%;left:0}.close-button__inner::before,.close-button__inner::after{content:"";position:absolute;display:inline-block;background-color:red;width:100%;height:4px;top:0;left:0}.close-button__inner::before{-webkit-transform:rotate(45deg);transform:rotate(45deg)}.close-button__inner::after{-webkit-transform:rotate(-45deg);transform:rotate(-45deg)}

.list{padding:10px 0px;display:flex;flex-wrap:wrap;justify-content:space-evenly}

.loader{width:100%;height:100;display:flex;justify-content:center;align-items:center}

